import _ from 'lodash';
import { withRouter } from 'react-router-dom';
import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';

import { categoryIcons } from '../Landing.constants';
import './LandingCategories.scss';

class LandingCategories extends Component {
  renderIcon(icon, isLastOdd) {
    const { history } = this.props;

    return (
      <Col
        className={['button-container', isLastOdd ? 'justify-content-center' : ''].join(' ')}
        xs={6}
        key={`category-icon-${icon.route}`}
      >
        <button
          type="button"
          onClick={() => icon.url && history.push(icon.url)}
        >
          <img src={icon.src} alt={icon.alt} />
        </button>
      </Col>
    );
  }

  render() {
    const { history } = this.props;

    return (
      <Row className="landing-category-container" noGutters>
        <Col className="d-flex justify-content-center" xs={12}>
          <h3>What do you want to insure?</h3>
        </Col>
        {_.map(categoryIcons, (icon, index) => this.renderIcon(icon, index === categoryIcons.length - 1 && index % 2 === 0))}
        <Col className="d-flex justify-content-center cat-button" xs={12}>
          <Button
            variant="medium-flat-blue"
            width={270}
            height={60}
            onClick={() => history.push('/categories')}
          >
            <p><span className="plus fa fa-plus" /><span>More Categories</span></p>
          </Button>
        </Col>
      </Row>
    );
  }
}

LandingCategories.defaultProps = {
};

LandingCategories.propTypes = {
  history: PropTypes.object.isRequired,
};

export default withRouter(LandingCategories);
